'use client';
import { useState } from 'react';

export default function AdminPage() {
  const [songs, setSongs] = useState([
    { id: 1, title: '‡πÅ‡∏™‡∏á‡∏™‡∏∏‡∏î‡∏ó‡πâ‡∏≤‡∏¢', artist: 'Bodyslam', genre: 'Rock' },
    { id: 2, title: '‡∏ñ‡πâ‡∏≤‡πÄ‡∏ò‡∏≠‡∏£‡∏±‡∏Å‡∏â‡∏±‡∏ô‡∏à‡∏£‡∏¥‡∏á', artist: 'Three Man Down', genre: 'Pop' },
  ]);

  const [form, setForm] = useState({ title: '', artist: '', genre: '' });
  const [editId, setEditId] = useState(null);

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleAdd = () => {
    if (!form.title || !form.artist) return;
    setSongs([
      ...songs,
      { id: Date.now(), ...form },
    ]);
    setForm({ title: '', artist: '', genre: '' });
  };

  const handleDelete = (id) => {
    setSongs(songs.filter((song) => song.id !== id));
  };

  const handleEdit = (song) => {
    setEditId(song.id);
    setForm({ title: song.title, artist: song.artist, genre: song.genre });
  };

  const handleUpdate = () => {
    setSongs(
      songs.map((song) =>
        song.id === editId ? { ...song, ...form } : song
      )
    );
    setEditId(null);
    setForm({ title: '', artist: '', genre: '' });
  };

  return (
    <div className="max-w-3xl mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">üéß ‡∏à‡∏±‡∏î‡∏Å‡∏≤‡∏£‡πÄ‡∏û‡∏•‡∏á (Admin)</h1>

      <div className="bg-white rounded-xl shadow p-4 mb-6">
        <h2 className="text-xl font-semibold mb-2">{editId ? '‚úèÔ∏è ‡πÅ‡∏Å‡πâ‡πÑ‡∏Ç‡πÄ‡∏û‡∏•‡∏á' : '‚ûï ‡πÄ‡∏û‡∏¥‡πà‡∏°‡πÄ‡∏û‡∏•‡∏á'}</h2>
        <input
          type="text"
          name="title"
          placeholder="‡∏ä‡∏∑‡πà‡∏≠‡πÄ‡∏û‡∏•‡∏á"
          value={form.title}
          onChange={handleChange}
          className="border p-2 mr-2 mb-2 w-full"
        />
        <input
          type="text"
          name="artist"
          placeholder="‡∏ä‡∏∑‡πà‡∏≠‡∏®‡∏¥‡∏•‡∏õ‡∏¥‡∏ô"
          value={form.artist}
          onChange={handleChange}
          className="border p-2 mr-2 mb-2 w-full"
        />
        <input
          type="text"
          name="genre"
          placeholder="‡πÅ‡∏ô‡∏ß‡πÄ‡∏û‡∏•‡∏á (Pop, Rock...)"
          value={form.genre}
          onChange={handleChange}
          className="border p-2 mr-2 mb-2 w-full"
        />
        {editId ? (
          <button onClick={handleUpdate} className="bg-yellow-500 text-white px-4 py-2 rounded mr-2">‡∏≠‡∏±‡∏õ‡πÄ‡∏î‡∏ï</button>
        ) : (
          <button onClick={handleAdd} className="bg-blue-500 text-white px-4 py-2 rounded">‡πÄ‡∏û‡∏¥‡πà‡∏°</button>
        )}
      </div>

      <div className="bg-white rounded-xl shadow p-4">
        <h2 className="text-xl font-semibold mb-4">üìÑ ‡∏£‡∏≤‡∏¢‡∏ä‡∏∑‡πà‡∏≠‡πÄ‡∏û‡∏•‡∏á</h2>
        {songs.length === 0 ? (
          <p className="text-gray-500">‡∏¢‡∏±‡∏á‡πÑ‡∏°‡πà‡∏°‡∏µ‡πÄ‡∏û‡∏•‡∏á</p>
        ) : (
          <ul>
            {songs.map((song) => (
              <li key={song.id} className="flex justify-between items-center border-b py-2">
                <div>
                  <p className="font-semibold">{song.title}</p>
                  <p className="text-sm text-gray-600">{song.artist} ‚Ä¢ {song.genre}</p>
                </div>
                <div>
                  <button
                    onClick={() => handleEdit(song)}
                    className="text-yellow-500 hover:underline mr-2"
                  >
                    ‡πÅ‡∏Å‡πâ‡πÑ‡∏Ç
                  </button>
                  <button
                    onClick={() => handleDelete(song.id)}
                    className="text-red-500 hover:underline"
                  >
                    ‡∏•‡∏ö
                  </button>
                </div>
              </li>
            ))}
          </ul>
        )}
      </div>
    </div>
  );
}
